import {useWeb3} from "../provider/web3_provider";
import {useSendFn} from "../utils/web3_send";
import {useCallback, useEffect, useState,} from "react";
import {useSendModal} from "../widget/modal/send_modal";
import {Contract} from "web3-eth-contract";
import {useWallet} from "use-wallet";
import {userInfo} from "../provider/liquidity";
import BigNumber from "bignumber.js";
import {getBalanceFormat} from "../utils/formatBalance";


export const useDeposit = () => {
    const {contrcts} = useWeb3();
    const handleSendFn = useSendFn(contrcts.lpAddress);

    const handleDeposit = useCallback(async (pid: number, amount: string) => {
        try {

            let deposit = contrcts.lpContract.methods.deposit(pid, amount.toString());
            let data = await handleSendFn(deposit);
            return data.transactionHash;
        } catch (e) {
            throw e
        }
    }, [contrcts]);

    const sendFn = useSendModal(handleDeposit)

    return [sendFn];
}
