export const useInfo = (pid: number, cellID: string) => {
    const {contrcts, blockNumber, tokenMap} = useWeb3();

    const {account,} = useWallet();

    const contract = contrcts.lpContract;
    const tokenItem = tokenMap[cellID];

    const [data, setData] = useState<userInfo>({});

    const handleUserInfo = useCallback(async () => {
        const _userInfo = await contract.methods.userInfos(pid, account).call();
        const _reward = await contract.methods.reward().call();
        const _startTime = await contract.methods.startTime().call();
        const _endTime = await contract.methods.endTime().call();
        const _totalAllocPoint = await contract.methods.totalAllocPoint().call();
        const _pendingToken = await contract.methods.pendingToken(pid, account).call();
        const {allocPoint:_allocPoint, amount:_amount} = await contract.methods.poolInfos(pid).call();

        const {_reserve1:_tokenAmount, _reserve0:_ethAmount} = await tokenItem.contract.methods.getReserves().call();
        const _totalSupply = await tokenItem.contract.methods.totalSupply().call();


        const ethPerSunder = new BigNumber(_tokenAmount).div(_ethAmount).toNumber()
        const annualPoolRewards = new BigNumber(_reward)
            .times( 60 * 60 * 24 * 365)
            .times(_allocPoint)
            .div(_totalAllocPoint)
            .div(_endTime - _startTime)
            .div(ethPerSunder).toFixed();

        const annualOnerEtherRewards = new BigNumber(_amount)
            .times(_ethAmount)
            .div(_totalSupply)
            .times(2).toFixed();

        let  APY = '0';
        if(annualPoolRewards!== '0' && annualOnerEtherRewards !=='0'){
            APY =new BigNumber(annualPoolRewards).times(100).div(annualOnerEtherRewards).toFixed(2)
        }
        setData({
            ..._userInfo,
            totalReward: _reward,
            startTime: _startTime,
            endTime: _endTime,
            totalStaked: _amount,
            APY,
            pendingToken:_pendingToken
        })
    }, [contrcts, account, pid]);

    useEffect(() => {
        handleUserInfo();
    }, [blockNumber])

    return [data];
}
