import {Contract} from "web3-eth-contract";
import Web3 from "web3";
import {ContractMap} from "./token.config";

export class Contrcts {
    public lpContract: Contract;


    public lpAddress: string;


    constructor(web3: Web3, networkId: number,) {
        this.lpAddress = ContractMap.lpContract[networkId];
        this.lpContract = new web3.eth.Contract(ContractMap.lpContract.abi, this.lpAddress);
    }
}





