import React, {useCallback, useMemo, useState} from "react";
import {TokenInterFace} from "../../../web3/token.config";
import {useForm} from "react-hook-form";
import {getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import {InputWidget} from "../../../widget/base/input";
import {ApproveButton} from "../../../widget/base/approve_button";
import {ButtonWidget} from "../../../widget/base/button";
import {useDeposit, useHarvest} from "../../../server/lp_server";
import BigNumber from "bignumber.js";


export const StakeBottom: React.FC<{
    maxAmount: string,
    tokenItem: TokenInterFace,
    spenderAddress: string
}> = ({
          maxAmount,
          tokenItem,
          spenderAddress
      }) => {
    const {register, setValue, errors} = useForm();
    const [amount, setAmount] = useState(0);
    const MaxNumber = useMemo(() => {
        return getBalanceNumber(maxAmount || 0);
    }, [maxAmount]);

    const [deposit] = useDeposit();


    const handleLogin = useCallback(async () => {
        let _number = getMaxNumber(amount, new BigNumber(maxAmount));
        await deposit(tokenItem.pid, _number);
        setAmount(0);
    }, [maxAmount, amount]);

    return (
        <>
            <InputWidget errors={errors}
                         inputRef={register({
                             required: 'Please enter a number > 0',
                             max: {value: MaxNumber, message: `Max ${MaxNumber}`},
                         })}
                         type={'tel'}
                         label={'Input'}
                         name={'amount'}
                         onChange={(ev) => {
                             setAmount(ev.target.value);
                         }}
                         placeholder={'0.00'}
                         setValue={(ev, proportion) => {
                             setValue(ev, MaxNumber * proportion);
                             setAmount(MaxNumber * proportion);
                         }}
            >
                <div className={'balance'}>Bal: <span>{MaxNumber}</span></div>
            </InputWidget>
            <ApproveButton contract={tokenItem.contract} contractAddress={tokenItem.address}
                           spender={spenderAddress}>
                <div>
                    <ButtonWidget text={'Stake'} onClick={handleLogin}/>
                </div>
            </ApproveButton>
        </>
    );
}
