import React, {createRef, useRef, useState} from "react";
import {TextField, InputBase, withStyles, MenuItem, Select, Input} from "@material-ui/core";
import styled from "styled-components";
import {greens} from "../../theme/colors";


interface InputInterFace {
    label: string,
    unit?: string,
    onChange?: (e: any) => void,
    placeholder: string,
    setValue: (ev: string,proportion:number) => void,
    inputRef: React.Ref<any>,
    errors: any;
    name?: string,
    buttonText?: string,
    type?: string,
    sunTitle?: any,

    // max:string,
}


export const InputWidget: React.FC<InputInterFace> = ({
                                                          label,
                                                          placeholder,
                                                          setValue,
                                                          unit,
                                                          inputRef,
                                                          errors,
                                                          name,
                                                          type,
                                                          onChange,
                                                          sunTitle = '',
                                                          children
                                                      }) => {
    return (
        <>
            <LabelStyle>
                <p className={'left'}>{label}</p>
            </LabelStyle>
            <FlexStyle>
                <InputStyle>
                    <TextField name={name || label}
                               error={Boolean(errors[name || label])}
                               placeholder={placeholder}
                               unselectable={'off'}
                               fullWidth={true}
                               inputRef={inputRef}
                               type={type}
                               onChange={(ev) => onChange && onChange(ev)}
                    />
                    {
                        unit ?
                            <span>{unit}</span> : children
                    }
                </InputStyle>
            </FlexStyle>
            <HelperTextStyle>
                <p className={'err'}>{errors[name || label] ? errors[name || label].message : ''}</p>
                {sunTitle}
            </HelperTextStyle>
            <MaxWidget setValue={(ev) =>setValue(name || label,ev)}/>

        </>
    );
}

export const MaxWidget:React.FC<{ setValue: (proportion:number) => void,}> = ({setValue}) => {

    return (
        <MaxStyle>
            <p onClick={()=>setValue(.25)}>25%</p>
            <p onClick={()=>setValue(.5)}>50%</p>
            <p onClick={()=>setValue(.75)}>75%</p>
            <p onClick={()=>setValue(1)}>MAX</p>
        </MaxStyle>
    );
}

const MaxStyle = styled.div`
  display: flex;
  margin-bottom: 20px;
  justify-content: space-around;

  p {
    border-radius: 5px;
    background: #E6EFFB;
    font-size: 14px;
    font-family: DINPro-Regular, DINPro;
    font-weight: 400;
    color: #000000;
    padding: 5px 20px;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0);
    cursor: pointer;
    &:hover{
      box-shadow: 0px 0px 7px 0px rgba(0, 117, 255, 0.27);
      border: 1px solid #0075FF;
    }
  }

`

