import {InputWidget} from "../../../widget/base/input";
import {ApproveButton} from "../../../widget/base/approve_button";
import {ButtonWidget} from "../../../widget/base/button";
import React, {useCallback, useMemo, useState} from "react";
import {useForm} from "react-hook-form";
import {useWeb3} from "../../../provider/web3_provider";
import {TokenInterFace} from "../../../web3/token.config";
import {getBalanceFormat, getBalanceNumber, getMaxNumber} from "../../../utils/formatBalance";
import styled from "styled-components";
import {useLiquiditContext} from "../../../provider/liquidity";
import {StakeBottom} from "./stake_widget";
import {WithdrawBottom} from "./withdraw_widget";
import {useHarvest} from "../../../server/lp_server";

export const BottomWidget: React.FC = () => {
    const {tokenMap, contrcts} = useWeb3();
    let spenderAddress = contrcts.lpAddress;
    const {cellID, user} = useLiquiditContext();

    const [behavior, setBehavior] = useState(0);
    const tokenItem = tokenMap[cellID];
    const [harvest] = useHarvest();


    return (<BottomStyle>
        <div className="tab_top">
            <h3 className={behavior == 0 ? 'ac' : ''} onClick={() => {
                setBehavior(0);
            }
            }>Stake</h3>
            <h3 onClick={() => {
                setBehavior(1);
            }} className={behavior == 1 ? 'ac' : ''}>Unstake</h3>
        </div>

        <div className="bgColor">
            <h2><span>Your staked</span><p>{getBalanceFormat(user?.amount)}</p></h2>
            <div style={{height: '8px'}}/>
            <h2><span>Available balance</span><p>{getBalanceFormat(tokenItem.balance)}</p></h2>
        </div>
        <div className="bgColor">
            <h2><span>Rewards</span><p>{getBalanceFormat(user?.pendingToken, 2)}</p></h2>
            <div className={'right'}>
                <ButtonWidget isDisable={user?.pendingToken === '0'}
                              text={'Claim Rewards'}
                              onClick={() => harvest(tokenItem.pid)}/>

            </div>

        </div>
        {behavior == 0 ?
            <StakeBottom tokenItem={tokenItem} maxAmount={tokenItem.balance} spenderAddress={spenderAddress}/> :
            <WithdrawBottom tokenItem={tokenItem} maxAmount={user?.amount || '0'} spenderAddress={spenderAddress}/>}
    </BottomStyle>);
}


const BottomStyle = styled.div`
  padding-bottom: 20px;
  box-sizing: border-box;

  .bgColor {
    border-radius: 8px;
    background: #F4F8FA;
    margin: 20px 0;
    padding: 10px 20px;
    box-sizing: border-box;

    h2 {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 16px;
        font-family: Raleway-Bold, Raleway;
        font-weight: bold;
        color: #51516E;
      }

      p {
        font-size: 16px;
        font-family: DINPro-Bold, DINPro;
        font-weight: bold;
        color: #000000;
      }
    }

    > div {
      margin-top: 10px;
      display: flex;
      justify-content: flex-end;

      > div {
        height: 30px;
        width: 40%;
      }
    }
  }

  .tab_top {
    display: inline-block;
    background: #E6EFFB;
    border-radius: 8px;
    overflow: hidden;


    h3 {
      display: inline-block;
      padding: 8px 20px;
      box-sizing: border-box;
      cursor: pointer;
      border-radius: 8px;

      border: 1px solid transparent;

      &.ac {
        background: #FFEDE8;
        border: 1px solid #0075FF;

      }
    }
  }
`;
