import {RightWidget} from "./widget/rigth";
import React, {createContext} from "react";
import styled from "styled-components";
import {WidthStyle} from "../../widget/style/width_style";
import {LiquidityProvider} from "../../provider/liquidity";
import {useWeb3} from "../../provider/web3_provider";
import {useInfo} from "../../server/lp_server";

export const MainPage: React.FC = () => {
    const {tokenMap} = useWeb3();
    const cellID = Object.keys(tokenMap)[0];
    const [userInfo] = useInfo(tokenMap[cellID].pid,cellID);
    return (
        <MainStylee>
            <LiquidityProvider token={tokenMap[cellID]} cellID={cellID} user={userInfo}>
                <RightWidget/>
            </LiquidityProvider>

        </MainStylee>
    )
}

export const StakeContext = createContext<LiquidityInterface>({
    key: '',
});

export interface LiquidityInterface {
    key: string,
}

