import React, {createContext, useContext} from "react";
import {TokenInterFace} from "../web3/token.config";


export const LiquidityProvider: React.FC<LiquidityInterface> = ({cellID, children,token,user}) => {
    return (
        <LiquiditContext.Provider value={{cellID: cellID,user:user,token:token}}>
            {children}
        </LiquiditContext.Provider>
    );
}

export const LiquiditContext = createContext<LiquidityInterface>({
    cellID: '',
    user:{}
});

export interface LiquidityInterface {
    cellID: string,
    user: userInfo
    token?:TokenInterFace
}

export interface userInfo{
    amount?: string,
    depositTime?: string,
    reward?: string,
    rewardDebt?: string,
    totalReward?: string,
    startTime?:string,
    endTime?:string,
    totalStaked?:string,
    APY?:string,
    pendingToken?:string,
}

export const useLiquiditContext = () => {
    let data = useContext(LiquiditContext);
    return data;
}
